/* ------------------
universal cards style
------------------ */
.card {
  border-radius: 20px;
  text-align: left;
}

.card:hover {
  background-color: #0C98FF;
  color: white;
}

.card i:hover {
  color: #000000;
}

#Calendar .card-header {
  text-align: center;
  color: white;
  border-radius: 20px 20px 0px 0px;
  background-color: #0C98FF;
}

#Calendar .card-footer {
  border-radius: 0px 0px 20px 20px;
}

.card i {
  color: #C6C6C6;
}

.card p {
  margin: 0;
}

/* ---------------------------
Hover CSS Located in index.css
--------------------------- */

/* ----------
Media queries
---------- */

/* -- Fully Responsive -- */

/* ------------------------------
On screens larger than 1751px
------------------------------ */
@media screen and (min-width: 1751px) {

  .card h1 {
    font-size: 65px;
  }

  .card h4 {
    font-size: 44px;
  }

  .card p {
    font-size: 36px;
  }

  .card i {
    font-size: 42px;
  }

}

/* ------------------------------
On screens between 1201px & 1750px
------------------------------ */
@media screen and (min-width: 1200px) and (max-width: 1750px) {

  .card h1 {
    font-size: 38px;
  }

  .card h4 {
    font-size: 24px;
  }

  .card p {
    font-size: 20px;
  }

  .card i {
    font-size: 24px;
  }

}

/* ------------------------------
On screens between 768px & 1199px
------------------------------ */
@media screen and (min-width: 768px) and (max-width: 1199px)  {

  .card h1 {
    font-size: 28px;
  }

  .card h4 {
    font-size: 18px;
  }

  .card p {
    font-size: 14px;
  }

  .card i {
    font-size: 22px;
  }

}

/* ------------------------------
On screens between 576px & 767px
------------------------------ */
@media screen and (min-width: 576px) and (max-width:767px) {

  .card h1 {
    font-size: 28px;
  }

  .card h4 {
    font-size: 18px;
  }

  .card p {
    font-size: 14px;
  }

  .card i {
    font-size: 22px;
  }

}

/* ------------------------------
On screens that are 575px or less
------------------------------ */
@media screen and (max-width: 575px) {

  .card h1 {
    font-size: 32px;
  }

  .card h4 {
    font-size: 20px;
  }

  .card p {
    font-size: 16px;
  }

  .card i {
    font-size: 22px;
  }

}
