#Team {
  height: auto;
}

#Team img {
  height: 120px;
  width: 125px;
  border-radius: 50%;
}


#Team i {
  color: black;
}

#Team i:hover {
  color: red;
  opacity: 100%;
}

#Team .team_m {
  transition: 0.1s;
  -webkit-transition: 0.1s;
  padding: 20px;
  border-radius: 10px;
}

.info {
  transition: 0.5s;
  -webkit-transition: 0.5s;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0C98FF;
  padding: 20px;
  border-radius: 10px;
  position: absolute;
  z-index: 10;
  display: none;
  color: white;
  text-align: left;
}

/* ---------------------------
Hover CSS Located in index.css
--------------------------- */

/* ----------
Media queries
---------- */

/* -- Fully Responsive -- */

/* ------------------------------
On screens larger than 1751px
------------------------------ */
@media screen and (min-width: 1751px) {


  #Team img {
    height: 200px;
    width: 200px;
  }

  #Team {
    height: 100vh;
    }

  #Team img {
    width: 210px;
    height: auto;
  }

  .team_m p {
    font-size: 32px;
  }

  .info {
    font-size: 20px;
  }

}

/* ------------------------------
On screens between 1201px & 1750px
------------------------------ */
@media screen and (min-width: 1200px) and (max-width: 1750px) {

  #Team {
    height: 100vh;
  }

  .team_m p {
    font-size: 18px;
  }

  .info p {
    font-size: 14px;
  }

}

/* ------------------------------
On screens between 768px & 1199px
------------------------------ */
@media screen and (min-width: 768px) and (max-width: 1199px)  {

  .team_m p {
    font-size: 18px;
  }

  .info p {
    font-size: 13px;
  }


}

/* ------------------------------
On screens between 576px & 767px
------------------------------ */
@media screen and (min-width: 576px) and (max-width:767px) {

  .team_m p {
    font-size: 18px;
  }

  .info p {
    font-size: 12px;
  }

}

/* ------------------------------
On screens that are 575px or less
------------------------------ */
@media screen and (max-width: 575px) {

  .team_m p {
    font-size: 18px;
  }

  .info p {
    font-size: 12px;
  }

}
